import React from 'react'
import { Link,  } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CImg,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react';

import { useSelector, useDispatch } from 'react-redux';
import { apiLogin, setFormLogin } from 'src/common/redux/action';


 const Login = () => {

    const { formLogin } = useSelector(state => state.loginReducer);
  const {
    UsernameOrEmail,
    Password
  } = formLogin;

  const dispatch = useDispatch();

  const btnLogin = () => dispatch(apiLogin(formLogin));

    return(
      <div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="8">
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <CForm>
                    <h1>Login</h1>
                    <p className="text-muted">Sign In to your account</p>
                    <CInputGroup className="mb-3">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-user" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput
                        type="text"
                        placeholder="Username / Email"
                        value={UsernameOrEmail}
                        onChange={(e) => dispatch(setFormLogin('UsernameOrEmail', e.target.value))}
                      />
                    </CInputGroup>
                    <CInputGroup className="mb-4">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-lock-locked" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput
                        type="password"
                        placeholder="Password"
                        value={Password}
                        onChange={(e) => dispatch(setFormLogin('Password', e.target.value))}
                      />
                    </CInputGroup>
                    <CRow>
                      <CCol xs="6">
                        <CButton color="primary"  className="px-4" onClick={btnLogin}>Login</CButton>
                      </CCol>
                      <CCol xs="6" className="text-right">
                        <CButton color="link" className="px-0">Forgot password?</CButton>
                      </CCol>
                    </CRow>
                  </CForm>
                </CCardBody>
              </CCard>
              <CCard className="text-whitepy-5 bg-dark d-md-down-none" style={{ width: '44%', backgroundColor:'rgb(52, 184, 196)'  }}>
                <CCardBody className="text-center">
                  <div>
                    <h2 style={{color:'white'}}>Sign up</h2>
                    <p style={{color:'white'}}>Aplikasi Sitem Monitoring dan Evaluasi</p>
                      <div>
                         <CImg src={process.env.PUBLIC_URL + '/Logo-only.png'} className="col-md-4 text-center"/>
                      </div>
                   {/*  <Link to="/monev">
                      <CButton color="primary" className="mt-3" active tabIndex={-1}>Register Now!</CButton>
                    </Link> */}
                  </div>
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>

    )
 }


export default Login
